<template>
  <div class="hs-container">
    <hs-scroll
      ref="tableView"
      class="table-view"
      :data="[details]"
      :options="tableViewOptions"
      @pulling-down="_getDetails">
      <ul class="bg" :style="{'background-image': `url('static/image/our-service/bg.png')`}">
        <div class="custom-cell-header" :type="language">
          <!-- <p class="title">客服</p> -->
          <img class="logo" src="static/image/our-service/logo.png"/>
          <p class="des">{{$t('base.Slogan')}}</p>
          <p class="tips">{{$t('base.HaveQuestions')}}</p>
        </div>
        <div class="custom-cell-card" :type="language" v-if="details.TcInfo" @click="_callPhone(details.TcInfo.Mobile)">
          <div class="left">
            <p class="title">{{$t('base.YourExclusiveTravelConsultant')}}</p>
            <p>{{details.TcInfo.Name}} {{details.TcInfo.Mobile}}</p>
          </div>
          <div class="right">
            <div class="logo" style="background-image: url('static/image/our-service/servicer.png')"></div>
          </div>
        </div>
        <div class="custom-cell-card" :type="language" @click="_callPhone('4006123123')">
          <div class="left">
            <p class="title">{{$t('base.NationalServicePhone')}}</p>
            <p>4006-123-123</p>
          </div>
          <div class="right">
            <div class="logo" style="background-image: url('static/image/our-service/24.png')"></div>
          </div>
        </div>
      </ul>
    </hs-scroll>
    <c-tabbar></c-tabbar>
  </div>
</template>
<script type="text/ecmascript-6">
import HsScroll from 'base/hs-scroll/hs-scroll'
import CTabbar from 'components/c-tabbar/c-tabbar'
import {kpiUserGetUserCenterInitInfo} from 'api'
import {mapMutations} from 'vuex'
export default {
  created() {
    this._getDetails()
  },
  data() {
    return {
      tableViewOptions: {
        pullDownRefresh: {
          threshold: 50
        }
      },
      details: {}
    }
  },
  methods: {
    ...mapMutations([
      'SET_PERSONAL_CENTER_DATA'
    ]),
    _getDetails() {
      kpiUserGetUserCenterInitInfo({}).then((res) => {
        console.log('强制刷新个人中心: ', res)
        this.details = res.ResultData
        this.SET_PERSONAL_CENTER_DATA(res.ResultData)
      }).catch(() => {
        this.$refs.tableView.forceUpdate()
      })
    },
    _callPhone(mobile) {
      window.location.href = `tel:${mobile}`
    }
  },
  components: {
    HsScroll,
    CTabbar
  }
}
</script>
<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/base'
@import '~common/stylus/variable'
.hs-container
  width 100%
  height 100%
  >.table-view
    width 100%
    height calc(100% - 50px)
    overflow hidden
    .bg
      background-size 100% auto
      background-position center top
      background-repeat no-repeat
      min-height 500px
      .custom-cell-header
        @extend .flex-column
        @extend .flex-start
        width 100%
        // background linear-gradient(to right, #E94746, #C40513)
        color $color_white
        // >.title
        //   @extend .flex-center
        //   width 100%
        //   height 50px
        //   font-weight bold
        //   font-size 1.2rem
        >.logo
          width 120px
          margin-top 10px
        >.des
          font-size 0.9rem
          margin-top 10px
        >.tips
          @extend .flex-center
          font-size 0.9rem
          height 30px
          padding 0 15px
          border-radius 15px
          background-color $color_black_boxshadow
          margin-top 30px
      .custom-cell-header[type="EN"]
        >.des
        >.tips
          font-size 0.7rem
      .custom-cell-card
        @extend.flex-row
        @extend.flex-between
        width 94%
        margin-left 3%
        padding 20px
        background-color $color_white
        border-radius 5px
        margin-top 15px
        box-shadow 0 0 10px $color_gray_boxshadow
        >.left
          width calc(100% - 40px)
          font-size 0.9rem
          color $color_black_text_1
          >.title
            font-size 1.1rem
            font-weight bold
            color $color_black_text_0
            margin-bottom 20px
        >.right
          width 40px
          >.logo
            width 36px
            height 36px
            background-size 100% 100%
            // border 2px solid #181842
      .custom-cell-card[type="EN"]
        >.left
          >.title
            font-size 0.9rem
</style>



